:root, *, ::before, ::after{
    box-sizing: border-box;
}

.container{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    display: flex;

    .cambrodgeDictFrame{
        flex-basis: 50%;
      
        iframe{
            width: 100%;
            height: 100%;
        }
    }



    .kannadaDictCntr{
        display: flex;
        flex-basis: 50%;
        flex-direction: column;
        
        .kanaja, .englishKannada{
            flex-basis: 100%;
    
            iframe{
                width: 100%;
                height: 100%;
            }
        }
    }
}

